@import '../_mixins.scss';
@import '../_variables.scss';
@import '../_variables-en.scss';

.si-popup {
    
    .si-popup {
        &header {
            position: relative;
            width: 100%;
            height: 52px;
            @include linearGradient(-45deg, #2a0835 50%, #0e0842 100%);
            @extend %flexbox;
            justify-content: center;
            align-items: center;
            @include border-radius(50px);
            font-family: $bold_font-family;
            font-weight: $bold_font-weight;
            font-size: 18px;
            text-transform: capitalize;
            color: $white;
        }

        &-close {
            right: 18px !important;
            top: 11px !important;
            z-index: 1 !important;
            // background: transparent !important;
            border: 1px solid;
            width: 25px;
            height: 25px;

            &:after {
                top: 3px;
                position: absolute;
                left: 3px;
            }

            .si-icon {
                &.si-closeBtn {
                    width: auto;
                    height: auto;

                    &::after {
                        // content: "\e908";
                        // border: 1px solid $white;
                        // color: $white;
                        // font-size: 11px;
                    }
                }
            }
        }
        

      
    }

    & &wrapper {
        padding: 10px;
    }

    & &-modal {
        @include border-radius(30px 30px 12px 12px);

        &.si-playerPopup {
            max-width: 1116px;
            width: 100%;
            .si-tbls {
                &__cell {

                    &.si-match,&.si-strikrate{
                        flex:0 0 10%;
                        width:10%
                    }
                    &.si-runs,&.si-wicket,&.si-Catches{
                        flex:0 0 7%;
                        width:7%
                    }
                    &.si-eco,&.si-batsman,&.si-bowler{
                        flex:0 0 12%;
                        width:12%
                    } 
                    &.si-allrounder{
                        flex: 0 0 14%;
                        width:14%;
                    }
                    &.si-WK{
                      flex:0 0 9%;
                      width:9%;
                    }
                } 
      
            } 
          
            .si-bold{
                border: 1px solid rgba($black, 0.2);
                border-top:0;
                .si-tbls__cell{
                font-family: $bold_font-family;
                font-weight: $bold_font-weight;
                }
            }
        }
    }



    .si-tbls {
      height: calc(100vh - 234px);
      overflow: hidden;
      border: 1px solid rgba($black, 0.2);
    
      &__head {
          background-color: #dfdfdf;
          //@include border-radius(15px);
    
          .si-tbls {
              &__row {
                  height: 40px;
                  border-bottom: none;
              }
    
              &__cell {
                  text-align: center;
                  font-family: $bold_font-family;
                  font-weight: $bold_font-weight;
                  text-transform: capitalize;
                  color: $client-secondary-color;
                  font-size: 14px;
    
              }
          }
      }
    
      .si-whiteBox {
          height: calc(100% - 40px);
    
          .swiper-container {
              height: 100%;
          }
    
          .swiper-scrollbar-drag {
              background: $client-secondary-color
          }
      }
    
      &__body {
          background-color: $white;
          &__row{
          
        }
      }
    
      &__row {
          @extend %flexbox;
          align-items: center;
          justify-content: flex-start;
          height: 50px;
    
          &:not(:last-child) {
            border-bottom: 1px solid rgba(0, 0, 0, 0.2);
          }
    
    
       
      }
    
      &__cell {
        //  flex:0 0 10%;
        //   width: 10%;
          padding: 10px;
          font-family: $regular_font-family;
          font-weight: $regular_font-weight;
          text-transform: capitalize;
          font-size: 14px;
          color: $font-color;
          text-align:center;
    
      
    
          
      }
    }
    

}




